@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhaijaan+2&family=Montserrat&family=Poppins&family=Raleway:wght@200;400&display=swap);
html{
  font-family: 'Baloo Bhaijaan 2', cursive;
}
body{
	margin: 0;
  
  background-image: url(/static/media/Background.36d870cb.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  box-shadow:inset 0 0 0 2000px rgba(26, 26, 26, 0.55);
  -webkit-backdrop-filter: blur(5px); /* Use for Safari 9+, Edge 17+ (not a mistake) and iOS Safari 9.2+ */
  backdrop-filter: blur(5px); /* Supported in Chrome 76 */
}

*, *::after, *::before{
	box-sizing: border-box;
}

main{
  min-height: calc(100vh - 211px - 58px);
}

.InvalidField{
  color: rgb(225, 56, 56);
  font-size: 18px;
  margin-top: -30px;
}

a{
  color: white;
}

.Link-Without-Decoration{
  text-decoration: none;
}
.NavBar{
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 0px 60px;

    margin-bottom: 100px;

    background-color: #181818a7;
    color: white;
}

.NavBar-Menu ul{
    display: flex;
    justify-content: space-between;

    align-items: center;
    list-style: none;
    text-decoration: none;
}

.NavBar-Menu ul li, .NavBar-Account li{
    margin: 0px 10px;
}

.NavBar-Account{
    display: flex;
    justify-content: space-between;

    align-items: center;
    list-style: none;
    text-decoration: none;
}


.NavBar-Menu-Button{
    list-style: none;
    text-decoration: none;

    color: white;
}

.SignOut:hover{
    cursor: pointer;
}
.Login{
    color: white;
    background-color: #181818a7;
    font-size: 30px;

    height: 580px;

    padding: 50px 100px;
    margin: 0px 470px;
    border-radius: 25px;
    text-align: center;
}


#Login-Form-Email, #Login-Form-Username, #Login-Form-Password{
  width: 100%;
}

#Login-Form{
    padding: 40px;
    line-height: 40px;
    text-align: center;
}
.Register{

    color: white;
    background-color: #181818a7;
    font-size: 30px;

    height: 740px;

    padding: 50px 100px;
    margin: 0px 70px;
    border-radius: 25px;
    text-align: center;
}


#Register-Form-Email, #Register-Form-Username, #Register-Form-Password, #Register-Form-Repeat-Password, #Register-Form-ImageURL{
  width: 510px;
}

#Register-Form{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    padding: 40px;
    line-height: 40px;
    text-align: left;
}

#UserPreviewImage-parent{
    width: 500px;
    height: 400px;
}

#UserPreviewImage{
    object-fit: cover;
    overflow: hidden;
    border: white 10px solid;
    width: 100%;
    height: 100%;
}
.Home{

    color: white;
    background-color: #181818a7;
    font-size: 30px;

    padding: 50px 100px;
    margin: 0px 250px;
    border-radius: 25px;
    text-align: center;
}

.Home-Welcome{

    color: white;
    background-color: #181818a7;
    font-size: 30px;

    padding: 50px 100px;
    margin: 0px 250px 25px 250px;
    border-radius: 25px;
    text-align: center;
}

.Home-Slider{
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-top: 50px;

    position: relative;
}

.Home-Items{
    margin: 50px 0px;
}

.Home-Items-List{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;

    list-style: none;
}

.Home-Items-List-Item{
    position: relative;
    margin-right: 2.5%;
    width: 800px;
    height: 500px;
    text-align: left;
}

.Home-Items-List-Item-Title{
    position: absolute;
    text-align: left;
    padding: 10px;
    bottom: 20px;
    left: 10px;

    font-size: 30px;
    text-shadow: rgb(0, 0, 0) 5px 0 10px;
    /*background-color: #181818;
    border-radius: 15px;*/
}

.Home-Items-List-Item-Date{
    position: absolute;
    text-align: left;
    padding: 10px;
    bottom: 0px;
    left: 10px;

    font-size: 20px;
    text-shadow: rgb(0, 0, 0) 5px 0 10px;
    /*background-color: #181818;
    border-radius: 15px;*/
}

.Home-Items-List-Item img{
    object-fit: cover;
    overflow: hidden;
    border: white 10px solid;
    width: 100%;
    height: 100%;
}

.slide{
    display: none;
}

.active{
    display: block;
}

.Home-Item-Previous{
    color: #ffffffd9;
    font-size: 100px;
}

.Home-Item-Next {
    color: #ffffffd9;
    font-size: 100px;
}

.Home-Item-Previous :hover{
    color: #ffffff3e;
    cursor: pointer;
}

.Home-Item-Next :hover{
    color: #ffffff3e;
    cursor: pointer;
}


.Memories{

    color: white;
    background-color: #181818a7;
    font-size: 30px;

    padding: 50px 100px;
    margin: 0px 315px;
    border-radius: 25px;
    text-align: center;
}

.Memories article{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: start;
    align-items: center;
}

.Memories article a{
    margin: 10px;
}

.Memories-Items{
    margin: 50px 0px;
}

.Memories-Items-List-Item{
    position: relative;

    width: 300px;
    height: 300px;
}
.Memories-Items-List-Item-Title{
    position: absolute;
    text-align: left;
    padding: 10px;
    bottom: -5px;
    left: 10px;

    font-size: 20px;
    text-shadow: rgb(0, 0, 0) 5px 0 10px;
    /*background-color: #181818;
    border-radius: 15px;*/
}

.Memories-Items-List-Item-Date{
    position: absolute;
    text-align: left;
    padding: 10px;
    bottom: -15px;
    left: 10px;

    font-size: 15px;
    text-shadow: rgb(0, 0, 0) 5px 0 10px;
    /*background-color: #181818;
    border-radius: 15px;*/
}

.Memories-Items-List-Item img{
    object-fit: cover;
    overflow: hidden;
    border: white 10px solid;
    width: 100%;
    height: 100%;
}


.Memory-Details{
    display: flex;
    justify-content: space-evenly;

    margin-top: 50px;

    color: white;
    background-color: #181818a7;
    font-size: 30px;

    padding: 50px 100px;
    margin: 0px 70px;
    border-radius: 25px;
    text-align: center;
}

.Memory-Details-Item{
    width: 800px;
    height: 500px;
    position: relative;
    margin-right: 75px;
}

.Memory-Details-Item img{
    object-fit: cover;
    overflow: hidden;
    border: white 10px solid;
    width: 100%;
    height: 100%;
}

.Memory-Details-Item-Title{
    position: absolute;
    text-align: left;
    padding: 10px;
    bottom: 20px;
    left: 10px;

    font-size: 30px;
    text-shadow: rgb(0, 0, 0) 5px 0 10px;
}

.Memory-Details-Item-Date{
    position: absolute;
    text-align: left;
    padding: 10px;
    bottom: 0px;
    left: 10px;

    font-size: 20px;
    text-shadow: rgb(0, 0, 0) 5px 0 10px;
}

.Memory-Details-Item-Description{
    text-align: center;
    font-size: 22px;
    width: 50%;
}

.Memory-Interactions{
    display: flex;
    justify-content: space-between;
    align-items: center;


    color: white;
    background-color: #181818a7;
    font-size: 30px;

    
    padding: 50px 100px;
    margin: 10px 70px;
    border-radius: 25px;
    text-align: center;
}

.Memory-Interactions button{
    background-color: #ffffffd9;
    color: #000000b3;
    font-size: 24px;
    width: 25%;
    height: 45px;
}

.Memory-Interactions Link{
    text-decoration: none;
}

.Memory-Interactions button:hover{
    background-color: #ffffff3e;
    color: white;
    font-size: 24px;
    width: 25%;
    height: 45px;
}
.Share{

    color: white;
    background-color: #181818a7;
    font-size: 30px;

    height: 740px;

    padding: 50px 100px;
    margin: 0px 70px;
